.home {
  height: 500px;
  background: url("./home-bg.jpg");
  background-size: cover;
  background-position: center;
  text-align: center;
  position: relative;
}

.home .home-information {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.home .home-information .home-title {
  font-size: 60px;
  font-weight: bold;
  color: #5e5e5e;
}

.home .home-information .home-info {
  font-size: 35px;
  color: #eb5424;
  margin-bottom: 20px;
}

.home .home-information .home-desc {
  font-size: 20px;
  line-height: 1.5;
  color: #888;
  margin-bottom: 20px;
}

.home .home-information .home-desc span {
  color: #000;
}

.home .home-information .home-btn {
  background: #eb5424;
  color: #fff;
  font-size: 18px;
  width: 150px;
  border: 0;
  padding: 15px;
  cursor: pointer;
  font-weight: bold;
}

.home .home-information .home-btn:hover {
  background: #fff;
  color: #eb5424;
}

@media (max-width: 768px) {
  .home .home-information .home-title {
    font-size: 40px;
    color: #fff;
  }

  .home .home-information .home-info {
    font-size: 20px;
  }

  .home .home-information .home-desc {
    font-size: 15px;
  }
}

@media (max-width: 575px) {
  .home .home-information .home-title {
    font-size: 30px;
  }
}
